<template>
  <el-dialog @close="reset" title="新增" :visible.sync="info.isShow">
    <el-form ref="formRef" :model="formData" label-width="90px">
      <el-form-item label="轮播图名称" prop="title">
        <el-input v-model="formData.title"></el-input>
      </el-form-item>

      <el-form-item label="图片" prop="img">
        <el-upload
          action="#"
          :auto-upload="false"
          list-type="picture-card"
          :limit="1"
          :on-exceed="limitTips"
          :on-change="imgChange"
          :file-list="imgList"
        >
          <i class="el-icon-plus"></i>
        </el-upload>
        <el-dialog :visible.sync="dialogVisible">
          <img width="100%" :src="dialogImageUrl" />
        </el-dialog>
      </el-form-item>
      <el-form-item label="状态" prop="status">
        <el-radio-group v-model="formData.status">
          <el-radio :label="1">正常</el-radio>
          <el-radio :label="2">禁用</el-radio>
        </el-radio-group>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="reset">取 消</el-button>
      <el-button type="primary" @click="submit">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
import vm from "../../utils/vm";
export default {
  props: ["info"],
  created() {
    vm.$on("sendBannerData", (row) => {
      this.formData = row;
      this.info.isShow = true;
      this.imgList =
        row.img !== "" ? [{ url: this.$imgBaseUrl + row.img }] : [];
    });
  },
  data() {
    return {
      dialogImageUrl: "",
      dialogVisible: false,
      activeName: "first",
      imgList: [],
      formData: {
        img: "",
        status: "",
        title: "",
      },
    };
  },
  methods: {
    specsChange(id, flag = true) {
      // 1-根据id查找索引
      const index = this.specsList.findIndex((item) => item.id === id);
      // 2-更新this.specsAttrs
      console.log(this.specsList[index]);
      this.specsAttrs = this.specsList[index].attrs;
      //   鼠标点击规格属性下拉框的时候, 需要进行数据重置
      if (flag) {
        // 3-重置默认的规格属性值
        this.formData.specsattr = [];
      }
    },
    // 监听用户选择的图片
    imgChange(file) {
      // 手动同步数据
      this.formData.img = file.raw;
    },
    limitTips() {
      this.$error("此处只能上传一张图片");
    },
    submit() {
      this.$refs.formRef.validate((valid) => {
        if (valid) {
          const fd = new FormData();
          for (let key in this.formData) {
            fd.append(key, this.formData[key]);
          }
          const url = this.formData.id > 0 ? "/banneredit" : "/banneradd";
          this.$http.post(url, fd).then((res) => {
            if (res.code === 200) {
              this.$success(res.msg);
              //  通知父组件更新数据
              this.$emit("updateList");
              //  重置数据
              this.reset();
            } else {
              this.$error(res.msg);
            }
          });
        }
      });
    },
    reset() {
      this.formData = {
        img: "",
        status: "",
        title: "",
      };
      this.$refs.formRef.resetFields();
      // 隐藏对话框组件
      this.info.isShow = false;
      // 删除图片预览
      this.imgList = [];
    },
  },
};
</script>

<style>
</style>


